/************ FONTS & GENERAL **********/
@font-face {
    font-family: 'Kaoly';
    src: url(/nralibera.github.io/static/media/Kaoly.f69aacca.ttf) format('truetype');
}

@font-face {
    font-family: 'Coolvetica';
    src: url(/nralibera.github.io/static/media/coolvetica-regular.651b51cc.otf) format('opentype');
}

#root {
    height: 100%;
    width: 100%;
}

html {
    width: 100%;
    height: 100%;
    background: url(/nralibera.github.io/static/media/background.be3327f6.jpg) no-repeat center center fixed;
    background-size: cover;
    background-repeat: no-repeat;
}


body {
    width: 100%;
    height: 100%;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.headerLinks > a {
    white-space: nowrap;
    font-family: "Coolvetica";
    opacity: 0.8;
    transition: all 0.3s;
}

    .headerLinks > a:link, a:hover, a:visited, a:active {
        color: white;
        text-decoration: none;
    }

    .headerLinks > a:hover {
        opacity: 1;
    }

.contentHome{
    display:flex;
    justify-content:center;
}

    .contentHome .title {
        font-family: "Kaoly";
        color: white;
        font-size: 10rem;
        padding-top: 10rem;
    }

.header_app {
    width:100%;
    height: 5em;
    display: flex;
    justify-content: center;
    color: #333333;
    z-index:0;
}

.header_app .menu {
    display: flex;
    flex-direction : row;
    justify-content: center;
    /* align-items: flex-start; */
    font-family:"Coolvetica";
    z-index: 0;
}

.header_app .menu-title {
    margin:0.7em 2em 0 0;
}



.app {
    width:100%;
    height:100%;

}

